export default {
  data () {
    return {
       contentPanel:{}
    }
  },
  components: {
  },
  onLoad(){
      this.init();
  },
  methods: {
      init(){
        var urls='https://v1.alapi.cn/api/mryw/random';
          uni.request({
			  url:urls,
          	success: (res) => {
          		this.contentPanel = res.data.data;
          		console.log(this.contentPanel)
          	}
          })
      }
  }
}
